import React from 'react';
import Link from 'gatsby-link';

const IndexPage = () => (
  <section className="home container">
    <h1 className="home__title title">Hi people</h1>
    <p>My name is Serhii</p>
    <p>I am an experienced and self-motivated Front-End developer with a strong technical skill-set and attention to details.</p>
  </section>
)

export default IndexPage;



